.jekyll-linkpreview-wrapper {
  max-width: 770px;
  margin: -90px 0 10px 0;
}

.jekyll-linkpreview-wrapper p {
    visibility: hidden;
  }

.jekyll-linkpreview-wrapper-inner {
  border: 1px solid rgba(0,0,0,.1);
  padding: 12px;
}

.jekyll-linkpreview-content {
  position: relative;
  max-height: 6em;
  min-height: 120px;
  overflow: hidden;
 /* margin-bottom: 10px; */
}

.jekyll-linkpreview-image {
  position: absolute;
  top: 0;
  right: 0;
}

.jekyll-linkpreview-image img {
  width: 120px;
  height: 120px;
  margin: auto;
}

.jekyll-linkpreview-body {
  margin-right: 34.5%;
}

.jekyll-linkpreview-body H2 {
    font-size: 1.6rem;
    margin-bottom: 5px;
}

.jekyll-linkpreview-title {
  font-size: 17px;
  margin: 0 0 2px;
  line-height: 1.3;
}

.jekyll-linkpreview-description {
  line-height: 1.5;
  font-size: 14px;
}

.jekyll-linkpreview-footer {
  font-size: 11px;
  margin: -7px 34.5% -12px 0;
}